import { type ClassValue, clsx } from "clsx"
import { twMerge } from "tailwind-merge"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

/**
 * Normaliza texto para b√∫squedas insensibles a tildes y caracteres especiales
 * @param text - El texto a normalizar
 * @returns Texto normalizado en min√∫sculas sin tildes ni caracteres especiales
 */
export function normalizeText(text: string): string {
  return text
    .toLowerCase()
    .normalize('NFD')
    .replace(/[\u0300-\u036f]/g, '') // Remover diacr√≠ticos (tildes)
    .replace(/[√±]/g, 'n') // Convertir √± a n
    .replace(/[√º]/g, 'u') // Convertir √º a u
    .replace(/[√ß]/g, 'c') // Convertir √ß a c
    .replace(/[√ü]/g, 'ss') // Convertir √ü a ss
    .replace(/[√¶]/g, 'ae') // Convertir √¶ a ae
    .replace(/[≈ì]/g, 'oe') // Convertir ≈ì a oe
    .replace(/[^a-z0-9\s]/g, '') // Remover caracteres especiales excepto espacios
    .replace(/\s+/g, ' ') // Normalizar espacios m√∫ltiples
    .trim();
}

/**
 * Busca texto normalizado dentro de otro texto normalizado
 * @param searchText - El texto a buscar
 * @param targetText - El texto donde buscar
 * @returns true si se encuentra la coincidencia
 */
export function searchInText(searchText: string, targetText: string): boolean {
  const normalizedSearch = normalizeText(searchText);
  const normalizedTarget = normalizeText(targetText);
  return normalizedTarget.includes(normalizedSearch);
}
